.contianer {
  background-color: #F3F5F8;
  min-height: 100vh;
  padding: 0.1px 0;

  .header {
    margin: 20px 20px;

    .header-card {
      position: relative;
      height: 150px;
    }

    .lan-info-board-indicator {
      position: absolute;
      margin: auto;
      justify-content: center;
      align-items: center;
      display: flex;
      width: 50px;
      right: 0px;
      left: 0px;
    }
  }

  .content {
    margin: 20px 20px;
  }
}

.row {
  display: block;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.row-space {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.column-center {
  display: block;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.text-overflow {
  box-sizing: border-box;
  ;
  display: block;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.strong {
  font-weight: bold;
  color: #444444;

}